@media only screen and (orientation: portrait) {
  .about {
    width: 100% !important;
  }
  .section-btn {
    padding: 0 15px 0 15px !important;
    font-size: 10px !important;
    width: 100% !important;
  }
  .section-icon {
    display: none;
  }
  .website-btn {
    width: 75% !important;
  }
  .website-icon {
    width: 15%;
    margin-left: 7px !important;
  }
  .code-btn {
    width: 80% !important;
  }
  .code-icon {
    width: 15%;
    margin-left: 7px !important;
  }
}

@media only screen and (min-device-width: 375px) and (max-device-width: 736px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: landscape) {
  .about {
    width: 90% !important;
  }
  .section-btn {
    padding: 0 15px 0 15px !important;
    font-size: 10px !important;
    width: 100% !important;
  }
  .section-icon {
    display: inline !important;
  }
  .website-btn {
    width: 32% !important;
  }
  .website-icon {
    width: 11%;
    margin-left: 7px !important;
  }
  .code-btn {
    width: 40% !important;
  }
  .code-icon {
    width: 18%;
    margin-left: 7px !important;
  }
}
.college-text {
  padding: 3%;
}
.name-paper h1 {
  font-weight: 400 !important;
}
.image {
  width: 10%;
  position: relative;
  left: 6% !important;
  top: 8px !important;
}
/* Skills */
.skill-item {
  -webkit-transition: all ease-in-out 0.3s !important;
  -o-transition: all ease-in-out 0.3s !important;
  transition: all ease-in-out 0.3s !important;
  margin: 2px 0px !important;
}
.skill-item:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}

/* Courses */
.course-item {
  -webkit-transition: all ease-in-out 0.3s !important;
  -o-transition: all ease-in-out 0.3s !important;
  transition: all ease-in-out 0.3s !important;
  margin: 3px 3px !important;
}
.course-item:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}

/* Projects */
.code-btn {
  width: 30%;
}

.code-icon {
  width: 13%;
  margin-left: 7px !important;
}

.website-btn {
  margin-left: 7px !important;
  width: 25%;
}
.website-icon {
  width: 15%;
  margin-left: 7px !important;
}

/* Experience */



